.header {
    height: 8.5rem;
    box-shadow: 0 2px 4px 0 rgba(0,0,0,.1);
    background-color: black;
    .wrap {
        position: absolute;
        max-width: 1450px;
        margin: 0 auto;
    
        .logo {
            width: 10.5rem;
            position: absolute;
            top: 55%; left: 6px;
            -webkit-transform: translateY(0%);
            -moz-transform: translateY(0%);
            transform: translateY(0%);
      
            img {
              display: block;
              width: 100%;
              margin: 0;
            }
          
        }
    }
}